'use client';

import { useState } from 'react';

export default function CounterPage() {
  // 前面是狀態，後面要更動前面的狀態
  const [count, setCount] = useState<number>(0);

  return (
    <>
      <h1>{count}</h1>
      <button
        onClick={() => {
          setCount(count + 1);
        }}
      >
        +1
      </button>
      <button
        onClick={() => {
          setCount(count - 1);
        }}
      >
        -1
      </button>
      <hr />
      {count ? <p>1. 目前 count 是 {count}</p> : null}
      {/* {count && <p>2. 目前 count 是 {count}</p>} */}
      {/* 上面這一行會顯示 count 的 0 但不會顯示 <p>2. 的內容</p> */}
      {!!count && <p>3. 轉成布林目前 count 是 {count}</p>}
    </>
  );
}
